* {
  margin: 0;
  padding: 0;
  
  margin-block-start: 0;
  margin-block-end: 0;

  margin-inline-start: 0;
  margin-inline-end: 0;

}

html, body, #root {
  height: 100%;
  width: 100%;
}

#root {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100%;
  font-family: 'Montserrat', sans-serif;
}

