.QuickFactsWrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  flex: 1;
}

.Fact {
  display: flex;
  gap: 20px;
  align-items: center; 
}

.Fact p {
  width: 300px;
}

@media (max-width: 1489px) {  
  .QuickFactsWrapper {
    width: 50%;
    gap: 20px;
    align-items: center;
  }  
}
